@charset "UTF-8";
/* Reset */
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

ul, ol {
  list-style: none;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  background: none;
  border: none;
  padding: 0;
  cursor: pointer;
  font-family: inherit;
}

.container {
  max-width: 1280px;
  margin: 0 auto;
}

@font-face {
  font-family: "Sora-Regular";
  src: url("../../src/assets/fonts/Sora-Regular.ttf");
}
@font-face {
  font-family: "Sora-Bold";
  src: url("../../src/assets/fonts/Sora-Bold.ttf");
}
@font-face {
  font-family: "Sora-ExtraBold";
  src: url("../../src/assets/fonts/Sora-ExtraBold.ttf");
  font-weight: 800;
}
@font-face {
  font-family: "Sora-SemiBold";
  src: url("../../src/assets/fonts/Sora-SemiBold.ttf");
}
body {
  font-family: "Sora-Regular", sans-serif;
  font-size: 16px;
  color: #71717A;
}

@media screen and (max-width: 1320px) {
  .container {
    margin: 0 20px;
  }
}
@media screen and (max-width: 1156px) {
  .hero .hero__content .hero__content-greeting {
    max-width: 400px;
  }
  .hero .hero__content .hero__content-description {
    max-width: 400px;
    margin-top: 80px;
  }
  .hero .hero__banner {
    top: 140px;
  }
  .hero .svg-size {
    width: 600px;
    height: auto;
  }
}
@media screen and (max-width: 860px) {
  .hero .hero__content {
    max-width: 349px;
    margin-top: 0;
  }
  .hero .hero__content .hero__content-description {
    margin-top: 20px;
  }
  .hero .hero__content .social_icon {
    margin-top: 30px;
  }
  .hero .hero__banner {
    top: 100px;
  }
  .hero .svg-size {
    width: 600px;
    height: auto;
  }
}
/* Медиа-запрос для мобильных устройств */
@media (max-width: 750px) {
  .hero {
    flex-direction: column-reverse;
  }
  .hero .hero__content {
    margin: 0 auto;
  }
  .hero .hero__content .hero__content-greeting {
    margin-top: 68px;
  }
  .hero .hero__content .hero__content-description {
    margin-top: 20px;
  }
  .hero .hero__content .social_icon {
    margin-top: 30px;
  }
  .hero .hero__banner {
    position: static;
    margin: 0 auto;
  }
  .hero .svg-size {
    width: 400px;
    height: auto;
  }
}
@media (max-width: 440px) {
  .hero {
    padding: 40px 0;
  }
  .hero .hero__content .hero__content-greeting {
    margin-top: 28px;
  }
  .hero .hero__content .hero__content-title {
    font-size: 28px;
  }
  .hero .svg-size {
    width: 330px;
    height: auto;
  }
}